import { createSlice } from '@reduxjs/toolkit'

const initialState = {
    InputText:""
}

const InputText = createSlice({
  name: "InputText",
  initialState,
  reducers: {
    setText: (state, action)=> {
        state.InputText = action.payload
    }
  }
});

export const {setText} = InputText.actions

export default InputText.reducer