import React from "react";
import DeleteIcon from "@mui/icons-material/Delete";
import IconButton from "@mui/material/IconButton";
import TaskAltIcon from "@mui/icons-material/TaskAlt";
import Tooltip from '@mui/material/Tooltip';
import { useDispatch } from "react-redux";
import { setTodo } from "../redux/Todos";

export default function ToDo({ todo, todos }) {
  const dispatch = useDispatch();
  const setTodos = (todos)=>dispatch(setTodo(todos))

  const deleteHandler = () => {
    setTodos(todos.filter((el) => el.id !== todo.id));
    // localStorage.setItem('TodoItems', todos.filter(JSON.stringify(todos.filter((el) => el.id !== todo.id))))
  };
  const completeHandler = () => {
    setTodos(todos.map((item) => {
      if (item.id === todo.id) {
        return {
          ...item,
          completed: !item.completed,
        };
      }
      return item;
    }));
  };
  return (
    <>
    <Tooltip title={todo.completed ? "Mark as Uncompleted":"Mark as completed"}>
      <IconButton sx={{...(todo.completed && {color:"green"})}} onClick={completeHandler} aria-label="completed">
        <TaskAltIcon />
      </IconButton>
    </Tooltip>
    <Tooltip title="Delete this Task">
      <IconButton onClick={deleteHandler} aria-label="delete">
        <DeleteIcon />
      </IconButton>
    </Tooltip>
    </>
  );
}
