import React from "react";
import InputBase from "@mui/material/InputBase";
import Button from "@mui/material/Button";
import ListItem from "@mui/material/ListItem";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import DiaLog from "./DiaLog";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import AlertMsg from "./AlertMsg";
// import FilterAltIcon from '@mui/icons-material/FilterAlt';
import Tooltip from "@mui/material/Tooltip";
import { useDispatch, useSelector } from "react-redux";
import { setText } from "../redux/InputText";
import { setTodo } from "../redux/Todos";

export default function AddToForm() {
  const [open, setOpen] = React.useState(false);
  const [openAlert, setOpenAlert] = React.useState(false);
  const [todoItems, setTodoItems] = React.useState(() => {
    // get the todos from localstorage
    const savedTodos = localStorage.getItem("todoItems");
    // if there are todos stored
    if (savedTodos) {
      // return the parsed the JSON object back to a javascript object
      return JSON.parse(savedTodos);
      // otherwise
    } else {
      // return an empty array
      return [];
    }
  });

  const dispatch = useDispatch();

  const InputText = useSelector((state)=> state.InputText.InputText);
  const setInputText = (text)=>dispatch(setText(text));
  const Todos = useSelector((state)=> state.Todos.Todos);
  const setTodos = (todos)=>dispatch(setTodo(todos));

  React.useEffect(() => {
    localStorage.setItem('todoItems', JSON.stringify(Todos));
  }, [Todos]);

  React.useEffect(() => {
    document.querySelector("#inputText").focus();
    if (todoItems) {
     setTodos(todoItems);
    }
  }, []);

  const inputTextHandler = (e) => {
    setInputText(e.target.value)
    // console.log(e.target.value)
  };

  const submitHandler = (e) => {
    e.preventDefault();
    if (document.querySelector("#inputText").value) {
      setTodos([
        ...Todos,
        {
          text: InputText,
          completed: false,
          id: Math.random().toString(36).substring("3", "9"),
        },
      ]);
      setInputText("");
    } else {
      setOpenAlert(true);
    }
  };

  const openDialog = () => {
    setOpen(true);
  };
  return(
    <>
      <Grid xs={12} item>
        <Paper>
          <ListItem>
            <Box
              component={"form"}
              sx={{ width: "100%", display: "flex" }}
              onSubmit={submitHandler}
            >
              <InputBase
                sx={{ ml: 1, flex: 1, width: 386 }}
                placeholder="Enter a task"
                inputProps={{ "aria-label": "Enter a task" }}
                onChange={inputTextHandler}
                value={InputText}
                id="inputText"
                autoComplete="off"
              />
              <Button type="submit" variant="contained" color="primary">
                Add
              </Button>
            </Box>
          </ListItem>
        </Paper>
      </Grid>
      <Grid
        xs={12}
        item
        sx={{
          position: "absolute",
          bottom: 15,
          left: 0,
          right: 0,
          paddingRight: "8px",
        }}
      >
        <Paper elevation={3}>
          <ListItem sx={{ justifyContent: "space-between" }}>
            <Typography>
              <b>Total Tasks: </b>
              {Todos.length}
            </Typography>
            {/* <Tooltip title="Filter Todo List">
              <Button type="submit" variant="contained">
                <FilterAltIcon /> Filter
              </Button>
            </Tooltip> */}
            <Tooltip title="Filter Todo List">
              <Button
                type="submit"
                variant="contained"
                color="error"
                onClick={openDialog}
              >
                Delete All
              </Button>
            </Tooltip>
          </ListItem>
        </Paper>
      </Grid>
      <DiaLog open={open} setOpen={setOpen} setTodos={setTodos}S />
      <AlertMsg openAlert={openAlert} setOpenAlert={setOpenAlert} />
    </>
  );
}
