import React from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
// import DialogContent from "@mui/material/DialogContent";
// import DialogContentText from "@mui/material/DialogContentText";
// import DialogTitle from "@mui/material/DialogTitle";
import Warning from "./Warning";

export default function DiaLog({setOpen, open, setTodos}) {

  const handleClose = () => {
    setOpen(false);
  };
  const delAll = ()=> {
    setTodos([]);
    setOpen(false);
  }
  
  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="draggable-dialog-title"
      >
        {/* <DialogTitle id="draggable-dialog-title" >
          Subscribe
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            To subscribe to this website, please enter your email address here.
            We will send updates occasionally.
          </DialogContentText>
        </DialogContent> */}
        <Warning type="warning" title="Warning" desc="This will delete all Todo List" strong="Are you sure?"/>

        <DialogActions>
          <Button autoFocus color={"success"} onClick={handleClose}>
            Cancel
          </Button>
          <Button color={"error"} onClick={delAll}>I'm sure</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
