import { createSlice } from '@reduxjs/toolkit'

const initialState = {
    Todos:[]
}

const Todos = createSlice({
  name: "Todos",
  initialState,
  reducers: {
    setTodo(state, action){
        return {
            ...state, 
            Todos : action.payload
        }
    }
  }
});

export const {setTodo} = Todos.actions

export default Todos.reducer